import React from 'react';
import RealtimeSearch from "./RealtimeSearch";

const unit = {};

let optionSample = {
    type : 'calc | watch | none',       // default watch
    target : '',                        // defualt undefined, calc-object, watch-string
    showIcon : '',                      // default true
    calcTag : '실제 아바타 값',
    calcTagImage : '',                  // default undefined
    defaultImage : '',                  // default undefined
    notiSoundType : ''
};

unit.Monster = () => {

    let monster = {
        title : 'MONSTER',
        type : 'package',
        target : {
            head : "몬스터 코스프레 패키지",
            body : ["몬스터 코스프레 칭호 상자", "몬스터 코스프레 오라 상자", "몬스터 코스프레 세라 상자"],
            tail : "몬스터 코스프레 아바타 풀세트 상자"
        },
        prefix : '몬스터 코스프레',
        notiSoundType: 0
    };

    let custom = {
        title : 'CUSTOM',
        type : 'watch',
        target : "퍼펙트 컨트롤",
        prefix : '몬스터 코스프레',
        notiSoundType: 1
    };

    return (
        <React.Fragment>
            <RealtimeSearch TITLE={monster.title} AVATAR={monster.target} OPTION={monster}/>
            <RealtimeSearch TITLE={custom.title} AVATAR={custom.target} OPTION={custom}/>
        </React.Fragment>
    )
};

unit.Emblem = () => {
    let TITLE = 'EMBLEM';
    let AVATAR = {
        head : "발할라 패키지",
        body : ["발할라 칭호 상자", "발할라 오라 상자", "발할라 보너스 아바타 상자", "발할라 세라 상자", "무기 클론 아바타 상자"],
        tail : "발할라 아바타 풀세트 상자"
    };
    return (<RealtimeSearch TITLE={TITLE} AVATAR={AVATAR} OPTION={{calc:false}}/>)
};

unit.Custom = () => {
    let TITLE = 'CUSTOM';
    let AVATAR = {
        head : "발할라 패키지",
        body : ["발할라 칭호 상자", "발할라 오라 상자", "발할라 보너스 아바타 상자", "발할라 세라 상자", "무기 클론 아바타 상자"],
        tail : "퍼펙트 컨트롤 합성법"
    };
    return (<RealtimeSearch TITLE={TITLE} AVATAR={AVATAR} OPTION={{calc:true}}/>)
};

unit.RareAvatar = () => {
    let TITLE = 'RAREAVATAR';
    let AVATAR = {
        head : "발할라 패키지",
        body : ["발할라 칭호 상자", "발할라 오라 상자", "발할라 보너스 아바타 상자", "발할라 세라 상자", "무기 클론 아바타 상자"],
        tail : "발할라 아바타 풀세트 상자"
    };
    return (<RealtimeSearch TITLE={TITLE} AVATAR={AVATAR} OPTION={{calc:false}}/>)
};

export default unit;